'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import PatientSidebar from '@/components/PatientSidebar';
import Loading from '@/components/Loading';
import api from '@/lib/api';
import { format, parseISO } from 'date-fns';
import { FiFileText } from 'react-icons/fi';

export default function PatientPrescriptionsPage() {
  const { user, loading: authLoading, logout } = useAuth();
  const router = useRouter();
  const [prescriptions, setPrescriptions] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!authLoading && (!user || user.role !== 'patient')) {
      router.push('/login');
      return;
    }
    if (user && user.role === 'patient') {
      fetchPrescriptions();
    }
  }, [user, authLoading, router]);

  const fetchPrescriptions = async () => {
    try {
      setLoading(true);
      const response = await api.get(`/prescriptions?limit=100`);
      let allPrescriptions = response.data.prescriptions || [];
      
      // Filter prescriptions for current patient
      allPrescriptions = allPrescriptions.filter((prescription: any) => 
        prescription.patient?.email === user?.email || prescription.patientId === user?.id
      );
      
      // Sort by date (most recent first)
      allPrescriptions.sort((a: any, b: any) => {
        const dateA = new Date(a.createdAt).getTime();
        const dateB = new Date(b.createdAt).getTime();
        return dateB - dateA;
      });

      setPrescriptions(allPrescriptions);
    } catch (error) {
      console.error('Error fetching prescriptions:', error);
    } finally {
      setLoading(false);
    }
  };

  if (authLoading || loading) {
    return <Loading />;
  }

  if (!user || user.role !== 'patient') {
    return null;
  }

  return (
    <div className="flex min-h-screen bg-gray-50">
      <PatientSidebar user={user} logout={logout} />
      <main className="ml-64 flex-1 p-8">
        <div className="max-w-7xl mx-auto">
          <h1 className="text-3xl font-bold text-gray-800 mb-8">My Prescriptions</h1>

          {/* Prescriptions List */}
          {prescriptions.length === 0 ? (
            <div className="bg-white rounded-lg shadow-sm p-12 text-center">
              <FiFileText className="text-6xl text-gray-300 mx-auto mb-4" />
              <p className="text-gray-500 text-lg">No prescriptions found</p>
            </div>
          ) : (
            <div className="space-y-4">
              {prescriptions.map((prescription) => (
                <div
                  key={prescription.id}
                  className="bg-white rounded-lg shadow-sm p-6 border border-gray-100 hover:shadow-md transition-shadow"
                >
                  <div className="flex items-start justify-between mb-4">
                    <div>
                      <h3 className="font-semibold text-gray-800 text-lg mb-1">
                        Prescription by Dr. {prescription.doctor?.user?.name || 'Unknown Doctor'}
                      </h3>
                      <p className="text-sm text-gray-600">
                        {format(parseISO(prescription.createdAt), 'MMM dd, yyyy')}
                      </p>
                    </div>
                  </div>

                  {prescription.diagnosis && (
                    <div className="mb-4">
                      <h4 className="font-medium text-gray-700 mb-1">Diagnosis:</h4>
                      <p className="text-gray-600">{prescription.diagnosis}</p>
                    </div>
                  )}

                  {prescription.medicines && prescription.medicines.length > 0 && (
                    <div className="mb-4">
                      <h4 className="font-medium text-gray-700 mb-2">Medicines:</h4>
                      <ul className="space-y-1">
                        {prescription.medicines.map((medicine: any, idx: number) => (
                          <li key={idx} className="text-gray-600">
                            â€¢ {medicine.name}
                            {medicine.dosage && ` - ${medicine.dosage}`}
                            {medicine.frequency && ` (${medicine.frequency})`}
                          </li>
                        ))}
                      </ul>
                    </div>
                  )}

                  {prescription.instructions && (
                    <div className="mb-4">
                      <h4 className="font-medium text-gray-700 mb-1">Instructions:</h4>
                      <p className="text-gray-600 whitespace-pre-wrap">{prescription.instructions}</p>
                    </div>
                  )}

                  {prescription.tests && prescription.tests.length > 0 && (
                    <div>
                      <h4 className="font-medium text-gray-700 mb-2">Tests Recommended:</h4>
                      <ul className="space-y-1">
                        {prescription.tests.map((test: string, idx: number) => (
                          <li key={idx} className="text-gray-600">â€¢ {test}</li>
                        ))}
                      </ul>
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>
      </main>
    </div>
  );
}
